/* Single posts navigation responsive styles - begin
    ====================================================================== */

@include phone-landscape {

	.edgtf-blog-single-navigation {

		.edgtf-blog-single-prev-holder,
		.edgtf-blog-single-next-holder {
			@include edgtfTableLayout();
			float: none;

			.edgtf-blog-single-thumb-wrapper,
			.edgtf-blog-single-nav-wrapper {
				@include edgtfTableCellLayout();
				width: 50%;
			}

			.edgtf-blog-single-prev,
			.edgtf-blog-single-next {
				display: inline-block;
				width: auto;
			}
		}

		.edgtf-blog-single-prev-holder {

			.edgtf-blog-single-nav-wrapper {
				text-align: right;
			}
		}

		.edgtf-blog-single-next-holder {

			.edgtf-blog-single-nav-wrapper {
				text-align: left;
			}

			.edgtf-blog-single-thumb-wrapper {

				img {
					margin: 0 0 0 auto;
				}
			}
		}
	}
}


/* Single posts navigation responsive styles - end
    ====================================================================== */