/* ==========================================================================
   Footer responsive style - begin
   ========================================================================== */

@include ipad-landscape {
    
    .edgtf-page-footer {
        
        .edgtf-column-content {
	        
	        @media only screen and (min-width: $phone-landscape-plus-pixel) {
		
		        &.edgtf-grid-col-3 {
			        width: 50%;
					float: left;
			        
			        &:nth-child(2n+1) {
				        clear: both;
			        }
			
			        &:nth-child(3),
			        &:nth-child(4) {
				        margin-top: 20px;
			        }
		        }
		
		        &.edgtf-grid-col-4 {
			        width: 33.33333333333333%;
			        float: left;
		        }
	        }
        }
	
	    .edgtf-footer-bottom-holder {
		    
		    .edgtf-footer-bottom-inner {
			    
			    .edgtf-grid-col-6 {
				    text-align: initial !important;
			    }
		    }
	    }
    }
}

@include ipad-portrait {

    .edgtf-page-footer {

        .edgtf-column-content {
            
            &:not(:first-child) {
                margin-top: 20px;
            }
        }
    }
}
/* ==========================================================================
   Footer responsive style - end
   ========================================================================== */