/* ==========================================================================
   Title responsive style - begin
   ========================================================================== */

@include laptop-landscape-medium {
    
    .edgtf-title {
    
        .edgtf-title-holder {
	
	        h7.edgtf-page-title {
		        font-size: 50px;
		        line-height: 64px;
	        }
        }
    }
}

@include ipad-landscape {
    
    .edgtf-title {
        
        &.edgtf-has-background {
            background: rgba(0, 0, 0, 0) none repeat scroll 0 0 !important;
            height: auto !important;
            padding: 0 !important;
        }
        
        &.edgtf-title-image-not-responsive {
            
            .edgtf-title-image {
                display: block;
            }
        }
        
        .edgtf-title-holder {
            position: absolute;
            top: 0;
            left: 0;
            display: block;
            height: 100% !important;
            padding: 0 !important;
            
            .edgtf-container {
                display: block;
                height: 100%;
            }
            
            .edgtf-container-inner {
                display: table;
                height: 100%;
            }
            
            .edgtf-title-subtitle-holder {
                display: table-cell;
                vertical-align: middle;
                width: 100%;
                padding: 0 !important;
            }
	
	        h7.edgtf-page-title {
		        font-size: 40px;
		        line-height: 54px;
	        }
        }
    }
}

@include ipad-portrait {
    
    .edgtf-title {
        
        &.edgtf-standard-type {
            
            .edgtf-breadcrumbs-holder {
                position: relative !important;
                top: auto !important;
                left: auto !important;
                right: auto !important;
                width: 100%;
            }
        }
	
	    .edgtf-title-holder {
		
		    .edgtf-subtitle {
			    font-size: 14px;
			    line-height: 24px;
			    margin: 8px 0 0;
		    }
	    }
    }
}

@include phone-landscape {
    
    .edgtf-title {
        
        .edgtf-title-holder {
            
            .edgtf-subtitle {
	            display: none;
            }
        }
    }
}
/* ==========================================================================
   Title responsive style - end
   ========================================================================== */